<template>
	<view class="mainList3">
		<view class="mar"></view>
<view class="swipercontain">
	<u-swiper style="swiper" :list="list3" previousMargin="20" nextMargin="20" acceleration circular :autoplay="true" radius="5"
		bgColor="#ffffff" click="clic(index)" @click="click()"></u-swiper>
</view>
		<view class="gongneng1">
			<view class="innergongneng1" @click="baojing">
				<image src="../../static/baojing6.png" mode=""></image>
				<view class="tips">报警</view>
			</view>
			<view class="innergongneng1" @click="tofuwu">
				<image src="../../static/daichuli2.png" mode=""></image>
				<view class="tips">服务</view>
			</view>
			<view class="innergongneng1" @click="weixiu">
				<image src="../../static/weixiufuwu2.png" mode=""></image>
				<view class="tips">维修</view>
			</view>
		</view>
		<view class="gongneng2" @click="tianyi">
			<image src="../../static/futianquan9.jpg" mode=""></image>
		</view>
		<view class="gongneng3">
			<view class="gg3partion gg3mar" @click="tojiankong">实况监控</view>
			<view class="gg3partion" @click="tonews">近期新闻</view>
			<view class="gg3partion gg3mar" @click="toxiangxixinxi">详细信息</view>
			<view class="gg3partion" @click="tolianxiwomen">联系我们</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				index: 10,
				ipc: "",
				weixiudidian: '',
				jiangxishen: ['南昌市', '赣州市', '宜春市'],
				beijingshi: ['东城区', '西城区', '朝阳区'],
				shanghaishi: ['黄浦区', '松江区', '崇明区'],
				guangdongsheng: ['广州市', '潮汕市', '东莞市'],
				shenfen: ['江西', '广州', '上海', '北京'],
				list3: [
					'../../static/futianquan2.jpg',
				],
			}
		},
		methods: {
			click(e){
				console.log(e);
			},
			tianyi() {
				uni.showModal({
					title: "注意安全",
					content: '',
					showCancel: false,
					cancelText: '收到',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			tonews() {
				uni.navigateTo({
					url: '/pages/news/news',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			tofuwu() {
				uni.navigateTo({
					url: '/pages/tuli/tuli'
				})
			},
			toxiangxixinxi() {
				uni.navigateTo({
					url: '/pages/xaingxixinxi/xaingxixinxi',
					success() {
						console.log('跳转到详细信息界面');
					}
				})
			},
			baojing() {
				uni,
				uni.getLocation({
					type: 'wgs84',
					geocode: 'true',
					success: function(res) {
						console.log(res)
					},
				});
				uni.showModal({
					title: '请求已收到',
					content: '',
					showCancel: false,
					success: () => {}
				});
			},
			weixiu() {
				let that = this
				let arr = this.shenfen
				uni.showActionSheet({
					title: '请选择省份',
					itemList: arr,
					success: function(res) {
						console.log('选中了' + arr[res.tapIndex]);
						that.weixiudidian = arr[res.tapIndex];
						if (that.weixiudidian == '江西') {
							uni.showActionSheet({
								title: '请选择具体地点',
								itemList: that.jiangxishen,
								success(res) {
									let ar = that.jiangxishen
									console.log(ar[res.tapIndex]);
									uni.showModal({
										title: '请求已收到',
										showCancel: false
									});
								}
							})
						} else if (that.weixiudidian == '北京') {
							uni.showActionSheet({
								title: '请选择具体地点',
								itemList: that.beijingshi,
								success(res) {
									let ar = that.beijingshi
									console.log(ar[res.tapIndex]);
									uni.showModal({
										title: '请求已收到',
										showCancel: false
									});
								}
							})
						} else if (that.weixiudidian == '上海') {
							uni.showActionSheet({
								title: '请选择具体地点',
								itemList: that.shanghaishi,
								success(res) {
									let ar = that.shanghaishi
									console.log(ar[res.tapIndex]);
									uni.showModal({
										title: '请求已收到',
										showCancel: false
									});
								}
							})
						} else {
							uni.showActionSheet({
								title: '请选择具体地点',
								itemList: that.guangdongsheng,
								success(res) {
									let ar = that.guangdongsheng
									console.log(ar[res.tapIndex]);
									uni.showModal({
										title: '请求已收到',
										showCancel: false
									});
								}
							})
						}
					}
				})
			},
			weizhixuanze() {
				uni.showActionSheet({
					itemList: ['南昌', '北京', '上海'],
					success: function(res) {
						console.log(res);
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					},
					fail: function(res) {
						console.log(res.errMsg);
					}
				});
			},
			tojiankong() {
				uni.switchTab({
					url: "/pages/list2/list2",
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			tolianxiwomen() {
				uni.navigateTo({
					url: "/pages/lianxiwomen/lianxiwomen",
				})
			},
		},
		onLoad() {}
	}
</script>

<style lang="scss" scoped>
	page{
		background-color: white;
	}
	.mainList3 {
		height: 100vh;
		width: 100vw;
		// background-color: #E2E2E2;

		.mar {
			height: 3vh;
			background-color: #f3f4f6;
			position: relative;
		}

		.swipercontain{
			border: 1rpx red solid;
			height: 24vh;
			.swiper {
			}
		}

		.gongneng1 {
			width: 660rpx;
			height: 16vh;
			margin: 2vh auto;

			.innergongneng1 {
				opacity: 0.9;
				width: 180rpx;
				height: 12vh;
				// background-image: linear-gradient(to top, #2c3e50, #3498db);
				background-color: #3498db;
				border: 3rpx black solid;
				border-radius: 24rpx;
				margin-top: 2vh;
				margin-left: 30rpx;
				float: left;
				position: relative;
				box-shadow: 0rpx 0rpx 2rpx white;

				image {
					width: 80%;
					height: 80%;
					position: absolute;
					left: 10%;
				}

				.tips {
					height: 4vh;
					line-height: 4vh;
					font-size: 24rpx;
					position: absolute;
					bottom: 0;
					text-align: center;
					width: 100%;
				}
			}
		}

		.gongneng2 {
			width: 640rpx;
			height: 16vh;
			margin: 20rpx auto;
			border: 1rpx red solid;
			border: red;
			border-radius: 48rpx;
			z-index: 22;

			image {
				opacity: 0.9;
				width: 100%;
				height: 100%;
			}
		}

		.gongneng3 {
			width: 720rpx;
			margin: 0rpx auto;

			.gg3partion {
				width: 240rpx;
				border: 1rpx black solid;
				float: left;
				margin-left: 80rpx;
				margin-top: 20rpx;
				border-radius: 64rpx;
				text-align: center;
				line-height: 12vh;
				color: black;
				background-image: linear-gradient(to bottom, #50C9C3, #96DEDA);
				box-shadow: 0rpx 0rpx 4rpx skyblue;
			}

			.gg3mar {
				margin-left: 80rpx;
			}

		}
	}
</style>